import { BrowserRouter, Route, Switch } from "react-router-dom";
import "./app.scss"


import MainPage from "./components/MainPage";
import DroneGrocery from "./components/projects/DroneGrocery/DroneGrocery";
import SortingVisualizer from "./components/projects/SortingVisualizer/SortingVisualizer";
import PokemonBook from "./components/projects/PokemonBook/PokemonBook";
import ARTetris from "./components/projects/ARTetris/ARTetris";
import ARPortal from "./components/projects/ARPortal/ARPortal";
import DungeonCrawler from "./components/projects/DungeonCrawler/DungeonCrawler";
import Mancity from "./components/projects/Mancity/Mancity";
import VRSolar from "./components/projects/VRSolar/VRSolar";
import ARSolar from "./components/projects/ARSolar/ARSolar";
import CarFactory from "./components/projects/CarFactory/CarFactory";
import StockPriceVisualizer from "./components/projects/StockPriceVisualizer/StockPriceVisualizer";


function App() {
  
  return (
    <div className="app">
      <BrowserRouter>
        <Switch>
          <Route exact path="/" component={MainPage}/>
          
          {/* Web App */}
          <Route exact path="/StockPriceVisualizer" component={StockPriceVisualizer}/>
          <Route exact path="/CarFactory" component={CarFactory}/>
          <Route exact path="/Virutal Reality Solar System" component={VRSolar}/>
          <Route exact path="/Manchester-City Fan Page" component={Mancity}/>
          <Route exact path="/Drone Grocery Delivery App" component={DroneGrocery}/>
          <Route exact path="/Sorting Visualizer" component={SortingVisualizer}/>
          <Route exact path="/Pokemon Book" component={PokemonBook}/>

          {/* Mobile App */}
          <Route exact path="/Augmented Reality Tetris Game" component={ARTetris}/>
          <Route exact path="/Augmented Reality Portal" component={ARPortal}/>
          <Route exact path="/Augmented Reality Solar System" component={ARSolar}/>

          {/* Game */}
          <Route exact path="/Dungeon Crawler" component={DungeonCrawler}/>

        </Switch>
      </BrowserRouter>
     {/* <MainPage/> */}
    </div>
  );
}

export default App;
