import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import PortfolioList from "../portfolioList/PortfolioList";
import "./portfolio.scss";
import {
  allPortfolio,
  webPortfolio,
  mobilePortfolio,
  gamePortfolio,
  // contentPortfolio,
} from "../../data";

export default function Portfolio() {
  const [selected, setSelected] = useState("featured");
  const [data, setData] = useState([]);
  const list = [
    {
      id: "all",
      title: "All",
    },
    {
      id: "web",
      title: "Web App",
    },
    {
      id: "game",
      title: "3D or Game",
    },
    {
      id: "mobile",
      title: "Mobile App",
    },
    // {
    //   id: "content",
    //   title: "Content",
    // },
  ];

  useEffect(() => {
    switch (selected) {
      case "all":
        setData(allPortfolio);
        break;
      case "web":
        setData(webPortfolio);
        break;
      case "mobile":
        setData(mobilePortfolio);
        break;
      case "game":
        setData(gamePortfolio);
        break;
      // case "content":
      //   setData(contentPortfolio);
      //   break;
      default:
        setData(allPortfolio);
    }
  }, [selected]);

  return (
    <div className="portfolio" id="portfolio">
      <br/>

      <h1>Portfolio</h1>
      <ul>
        {list.map((item) => (
          <PortfolioList
            title={item.title}
            active={selected === item.id}
            setSelected={setSelected}
            id={item.id}
          />
        ))}
      </ul>
      <div className="container">
        {data.map((d) => (
          <div className="item">
            <Link to={"/", d.title}>
              <img
                src={d.img}
                alt=""
                onClick={()=>{console.log(d.title, " is clicked")}}
              />
            </Link>
            
            <h3>{d.title}</h3>
          </div>
        ))}
      </div>
    </div>
  );
}
